
#footer {
  text-align: center;
  color: grey!important;
  width: 99%;
  position: relative;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin-bottom: 0px;
  z-index: 100;
  padding: 0;
  background-color: #282828!important;
  margin:0;
  padding: 0;
}

.footer-color {
  background-color: #282828!;
  position: absolute;
  margin: 0;
  padding: 0;
  z-index: 100;
}

.contact {
  display: inline-block;
  margin: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  vertical-align: middle;
  
  margin: 3vh 3vw;
  padding-right: 3vw;
  position: absolute;
 
}

.brand-romanesc {
  margin: 1%;
  position: absolute;
}

.produse-romanesti {
  position: absolute;
  margin: 1%;
}

.anpc {
  text-decoration: none;
  color: white;
}

.politica-de-confidentialitate {
  float: right;
  text-decoration: none;
  color: white;
}
.anpc.brand-romanesc a:hover {
  color: #6b6b6b;
}

.anpc.brand-romanesc a:visited {
  color: #6b6b6b;
}

.anpc.brand-romanesc a:active {
  color: #6b6b6b;
}

.anpc.brand-romanesc a:link {
  color: #6b6b6b;
}

