.wave {
  position: relative;
  top: 770px;
  left: -4%;
  margin: 0;
  padding: 0;
  width: 104%;
}

.wave2 {
  position: relative;
  top:1000px;
 
}

.App {
  position: relative;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0%;
  width: 104%;
}

body,
*,
html {
  padding: 0;
  margin: 0;
}

.form-control {
  border-radius: 10px;
  z-index: 1000;
}
#user-form {
  position: absolute;
}
.submit {
  position: absolute;
  text-transform: uppercase;
  padding: 5px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 12px;
  height: 35px;
  width: 130px;
  left: 30%;
  z-index: 100;
}
.user-form{
  z-index: 100;
}

@font-face {
  font-family: myFirstFont;
  src: url(./assets/Thirsty.ttf);
}
.quiz-title, .qtitle {
  font-family: myFirstFont;

}
.modal-congrats {
  font-family: myFirstFont;
  left: 30%;
  position: absolute;
  font-size: 44px;
  padding-bottom: 5px;
}
